<template>
  <div>
    <Row type="flex" justify="center"   >
      <Col span="2">
        <Tooltip content="用户名">
          <p style="background-color:lightseagreen;width: 80px;height:80px;border-radius: 5px;">
            <Icon type="ios-contact-outline" color="white" size="80"/>
          </p>
        </Tooltip>
      </Col>
      <Col class="info" span="1">
        {{userInfo.userName}}
        <span   v-if="this.userInfo.userId!==this.$store.state.userInfo.userId">
        <span v-if="this.userInfo.userGender==='F'">
           <Icon type="md-female" color="deeppink" size="20"/>
        </span>
        <span v-else>
          <Icon type="md-male" color="deepskyblue" size="20"/>
        </span>
          </span>
      </Col>

      <Col span="2" style="margin-left: 50px">
        <Tooltip content="文章数">
          <p style="background-color: gray;width: 80px;height:80px;border-radius: 5px;">
            <Icon type="ios-list-box" color="white" size="80"/>
          </p>
        </Tooltip>
      </Col>
      <Col class="info">
        {{userInfo.userTextsnum}}
      </Col>

      <Col span="2" style="margin-left: 50px">
        <Tooltip content="关注数">
          <p style="background-color: deeppink;width: 80px;height:80px;border-radius: 5px;">
            <Icon type="ios-heart" color="white" size="80"/>
          </p>
        </Tooltip>
      </Col>
      <Col class="info">
        {{userInfo.userFocusnum}}
      </Col>
      <Col span="2" style="margin-left: 50px">
        <Tooltip content="粉丝数">
          <p style="background-color: #2d8cf0 ;width: 80px;height:80px;border-radius: 5px;">
            <Icon type="ios-people" color="white" size="80"/>
          </p>
        </Tooltip>
      </Col>
      <Col class="info">
        {{userInfo.userFansnum}}
      </Col>
      <Col span="2" style="margin-left: 50px">
        <Tooltip content="收藏数">
          <p style="background-color: orange;width: 80px;height:80px;border-radius: 5px;">
            <Icon type="ios-star" color="white" size="80"/>
          </p>
        </Tooltip>
      </Col>
      <Col class="info">
        {{userInfo.userCollectionsnum}}
      </Col>
    </Row>
    <Row type="flex" justify="center" v-if="this.userInfo.userId===this.$store.state.userInfo.userId">
      <Col>
        <Card style="width:600px">
          <Form ref="updateInfo" :model="updateInfo" :rules="ruleCustom"
                :label-width="50">
            <FormItem>
              <Upload
                ref="upload"
                :on-success="handleSuccess"
                :format="['jpg','jpeg','png']"
                :max-size="2048"
                :on-format-error="handleFormatError"
                :on-exceeded-size="handleMaxSize"
                multiple
                action="http://106.14.137.176:8080/blog/file"
              >
                <Tooltip content="更换头像">
                  <img src="../../../static/img/default.jpg" class="round_icon" v-if="this.userInfo.userHeadportrait==='default.jpg'"></img>
                  <Head-portrait :src="this.userInfo.userHeadportrait" size="large" v-else></Head-portrait>
                </Tooltip>
              </Upload>
            </FormItem>
            <FormItem prop="userName">
              <Input type="text" v-model="updateInfo.userName" placeholder="请输入用户名"/>
            </FormItem>
            <FormItem>
              <Input type="text" v-model="updateInfo.userWords" placeholder="编辑个性签名"></Input>
            </FormItem>
            <FormItem prop="userPassword">
              <Input type="password" v-model="updateInfo.userPassword" placeholder="请输入密码"/>
            </FormItem>
            <FormItem prop="userEmail">
              <Input type="email" v-model="updateInfo.userEmail"></Input>
            </FormItem>
            <FormItem>
              <radio-group v-model="updateInfo.userGender">
                <Radio label="M" value="M">
                  <Icon type="md-male" color="deepskyblue" size="20"/>
                </Radio>
                <Radio label="F" value="F">
                  <Icon type="md-female" color="deeppink" size="20"/>
                </Radio>
              </radio-group>
            </FormItem>
            <FormItem>
              <Button type="primary" @click="handleSubmit('updateInfo')">修改个人信息</Button>
            </FormItem>
          </Form>
        </Card>
      </Col>
    </Row>
    <Row  type="flex" justify="center"  v-else>
      <Col >
        <span style="font-weight: bolder;font-size: 30px" v-if="this.userInfo.userWords!==''">{{this.userInfo.userWords}}</span>
        <span style="font-weight: bolder;font-size: 30px" v-else>这个人很懒什么都没有写</span>
      </Col>
    </Row>
  </div>
</template>

<script>
  import HeadPortrait from '../page/HeadPortrait'

  export default {
    name: "index",
    inject: ['reload'],
    data() {
      const validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        }
        callback()
      };
      const validateUsername = (rule, value, callback) => {
        if (value === '') {
          callback(new Error("用户名不能为空"))
        } else if (value !== this.userInfo.userName) {
          this.axios.get('user/' + this.updateInfo.userName).then(response => {
            if (response.data.code === 200) {
              callback()
            } else {
              callback("该用户名已被注册")
            }
          }).catch(error => {
            this.$Message.error("校验用户名失败")
          })
        }else if(value === this.userInfo.userName){
          callback()
        }
      }
      return {
        userInfo: {},
        updateInfo:{},
        ruleCustom: {
          userName: [
            {validator: validateUsername, trigger: 'blur'}
          ],
          userPassword: [
            {validator: validatePass, trigger: 'blur'}
          ]
          , userEmail: [
            {require: true, message: "邮箱不能为空", trigger: 'blur'},
            {type: 'email', message: '邮箱格式不正确', trigger: 'blur'}
          ]
        },userHeadportrait:''

     }
    }, methods: {
      getOne(){
        var params = new URLSearchParams();
        params.append('userName', this.$store.state.userInfo.userName);
        params.append('userPassword', this.$store.state.userInfo.userPassword);
        this.axios.post('login', params).then(response => {
          if (response.data.code ==100) {
            this.updateInfo=response.data.map.user
          }
        })
      }, handleSuccess(res) {
        this.updateInfo.userHeadportrait = res.map.headPortrait
      },
      handleFormatError(file) {
        this.$Notice.warning({
          title: '图片格式不正确',
          desc: file.name + ' 不是一张图片'
        });
      },
      handleMaxSize(file) {
        this.$Notice.warning({
          title: '超出文件大小限制',
          desc: '图片 ' + file.name + ' 大于2M'
        });
      },handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            var params = new URLSearchParams();
            params.append("userName", this.updateInfo.userName);
            params.append("userPassword", this.updateInfo.userPassword);
            params.append("userEmail", this.updateInfo.userEmail);
            params.append("userGender", this.updateInfo.userGender);
            params.append("userHeadportrait", this.updateInfo.userHeadportrait);
            params.append("userWords",this.updateInfo.userWords);
            this.axios.put("user/"+this.updateInfo.userId, params).then(response => {
              if (response.data.code === 100) {
                this.$Message.success('修改成功')
                //重新获取，用户信息
                this.getOne()
                sessionStorage.setItem("userInfo",JSON.stringify(this.updateInfo));
                this.$store.commit('setLogin')
                //返回当前页面
                this.reload()
              } else {
                this.$Message.error("修改失败")
              }
            }).catch(error => {
              this.$Message.warning('错误！修改失败')
            })
          } else {
            this.$Message.warning('数据验证失败!');
          }
        })
      },getUserInfo(userId){
        this.axios.get('userId/'+userId).then(res=>{
          if(res.data.code===100){
            this.userInfo=res.data.map.getUser
            if(this.userInfo.userId===this.$store.state.userInfo.userId){
              this.getOne()
            }
          }else{
            this.$Message.error("获取用户信息失败")
          }
        }).catch(error=>{
          this.$Message.warning("获取用户信息失败")
        })
      }
    }, components: {HeadPortrait}, mounted() {
      this.getUserInfo(this.$route.params.userId)

    }
  }
</script>

<style scoped>
  .info {
    font-size: 30px;
    font-weight: bolder;
    color: black;
    margin-top: 25px;
  }
  .round_icon:hover {
    transform: scale(1.4);
  }

  .round_icon {
    width: 50px;
    height: 50px;
    display: flex;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.6s;
  }
</style>



